.page {
  margin: 0;
  display: block;
}

.header {
  box-sizing: border-box;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0 50px;
  display: flex;
}

.header__logo_link {
  width: 40px;
  height: 40px;
  margin: 10px 0;
  display: block;
}

.header__nav {
  display: block;
}

.header__nav_list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.header__nav_link {
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.header__nav_item {
  margin-left: 20px;
}

.header__nav_item:first-child {
  margin-left: 0;
}

.is-active, .header__nav_link:hover {
  color: #00acdc;
  transition-duration: .3s;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
}

.card__image {
  margin-inline: 19px;
  width: 160px;
  height: 134px;
  margin-top: 32px;
  display: block;
}

.card:hover {
  transition-duration: .3s;
  transform: scale(1.2);
}

.card__title .card:hover {
  color: #34568b;
  transition-duration: .3s;
}

.card__title {
  color: #060b35;
  padding-inline: 16px;
  margin: 0;
  padding-top: 40px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  text-align: left;
  padding-inline: 16px;
  margin: 0;
  padding-top: 4px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.reviews {
  justify-content: space-between;
  align-items: flex-end;
  margin-inline: 16px;
  padding-top: 16px;
  display: flex;
}

.reviews__count {
  color: #060b35;
  text-align: bottom;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.price {
  margin-inline: 16px;
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 24px;
  display: flex;
}

.price__text {
  text-align: left;
  color: #616070;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.price__price {
  text-align: right;
  color: #060b35;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__to-buy {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  margin: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: flex;
}

.card__to-buy:hover {
  color: #00acdc;
  text-transform: uppercase;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  margin: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: flex;
}

.stars--4 {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4:last-child {
  margin: 0;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.container {
  grid-template-columns: repeat(auto-fill, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}
